.App {
  text-align: center;
  top: 80%;
  left: 50%;
  width: 60vw;
  position: absolute;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

* {
  pointer-events: none;
  cursor: none;
}

.progress-ring__circle {
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}